@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body{margin:0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;cursor:none}button{border:none}ul{padding:0;list-style:none}@media (min-width: 800px){.body-layout{display:flex}}

.persons-list{margin:32px 0;max-height:160px;overflow:scroll;border:1px solid #f7f7f7}@media (min-width: 800px){.persons-list{max-height:400px}}.error{color:red;font-size:20px;text-align:center;margin:20px 0}.persons-list__person{padding:4px 8px}.persons-list__person:nth-child(even){background-color:#f7f7f7}.persons-list__person:hover{-webkit-text-stroke-width:1px;-webkit-text-stroke-color:black}.persons-list__person .active .persons-list__person__content{-webkit-text-stroke-width:1px;-webkit-text-stroke-color:black}.persons-list__person:nth-child(even) .persons-list__person__content{-webkit-animation:from-right 1s ease-in-out;animation:from-right 1s ease-in-out}.persons-list__person:nth-child(odd) .persons-list__person__content{-webkit-animation:from-left 1s ease-in-out;animation:from-left 1s ease-in-out}@-webkit-keyframes from-right{0%{-webkit-transform:translateX(-40px);transform:translateX(-40px)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}@keyframes from-right{0%{-webkit-transform:translateX(-40px);transform:translateX(-40px)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}@-webkit-keyframes from-left{0%{-webkit-transform:translateX(40px);transform:translateX(40px)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}@keyframes from-left{0%{-webkit-transform:translateX(40px);transform:translateX(40px)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}.actions{display:flex;justify-content:center}.actions__modify-list{margin:0 8px;border-radius:4px;text-transform:uppercase;padding:14px 16px 14px 16px !important;font-size:14px;line-height:20px;text-align:center;color:#4a4a4a;font-weight:400;cursor:none;background-color:white;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1);transition:background-color 500ms linear}.actions__modify-list:hover{background-color:#f2f2f2;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:black}.persons-list-container{margin:8px 0}.persons-list__header{text-align:center;margin:32px 0}

.send{margin:32px 0;border-radius:4px;text-transform:uppercase;padding:14px 16px 14px 16px !important;font-size:14px;line-height:20px;text-align:center;color:#4a4a4a;font-weight:400;background-color:white;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1);border:none;transition:background-color 500ms linear}.send:hover{background-color:#f2f2f2;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:black}.edit-container{padding:8px}@media (min-width: 800px){.edit-container{padding:29px 0 0 32px}}.edit__img{border-radius:4px;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1)}.input-container{margin:8px 0}.input__key{margin-right:8px}.input__value{box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1);border:none;border-radius:4px;padding:4px}.invalid{border:1px solid red}

.details-container{padding:8px}@media (min-width: 800px){.details-container{padding:29px 0 0 32px}}.edit{margin:32px 0;border-radius:4px;text-transform:uppercase;padding:14px 16px 14px 16px !important;font-size:14px;line-height:20px;text-align:center;cursor:none;color:#4a4a4a;font-weight:400;background-color:white;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1);transition:background-color 500ms linear}.edit:hover{background-color:#f2f2f2;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:black}.details__contact__header,.details__names__header{font-size:20px;border-bottom:1px solid #f7f7f7;margin:16px 0}.details__img{border-radius:4px;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1);-webkit-animation:blow 1s ease-in-out;animation:blow 1s ease-in-out}.details__img-active{border-radius:4px;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1);-webkit-animation:blow-active 1s ease-in-out;animation:blow-active 1s ease-in-out}@-webkit-keyframes blow{0%{-webkit-transform:scale(0);transform:scale(0)}80%{-webkit-transform:scale(1.1);transform:scale(1.1)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes blow{0%{-webkit-transform:scale(0);transform:scale(0)}80%{-webkit-transform:scale(1.1);transform:scale(1.1)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes blow-active{0%{-webkit-transform:scale(0);transform:scale(0)}80%{-webkit-transform:scale(1.1);transform:scale(1.1)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes blow-active{0%{-webkit-transform:scale(0);transform:scale(0)}80%{-webkit-transform:scale(1.1);transform:scale(1.1)}100%{-webkit-transform:scale(1);transform:scale(1)}}.details__contact__phone,.details__contact__mail{display:flex;align-items:center}.details__contact__phone__icon,.details__contact__mail__icon{margin-right:8px}

.cursor-dot,.cursor-dot-outline{z-index:800;pointer-events:none;position:absolute;top:50%;left:50%;border-radius:50%;opacity:0;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);transition:opacity 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;transition:opacity 0.2s ease-in-out, transform 0.2s ease-in-out;transition:opacity 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out}.cursor-dot{width:8px;height:8px;background-color:#686768}.cursor-dot-outline{width:40px;height:40px;background-color:rgba(216,216,216,0.5)}@media (max-width: 1000px){.cursor-dot,.cursor-dot-outline{display:none}}

